
import { defineComponent, ref, onMounted } from 'vue'

import Navi from "../components/Navi.vue";
import OrderPro from "../components/OrderPro.vue"

import { useRouter, useRoute } from "vue-router"

import axios from 'axios'

import {Toast} from 'vant'

export default defineComponent({

  name: 'OrderBuy',

  components: {
    Navi,
    OrderPro,
  },

  setup () {

    let router: any = useRouter();

    let route: any = useRoute();
    
    let show: any = ref<boolean>(false);

    let chosenAddressId: any = ref<string>('');

    let list: any = ref<Array<any>>([]);

    let userSelectAddress: any = ref<any>({
        tel: '',
        name: '',
        isDefault: false,
        address: ''
      });
    
    //购物袋的sid
    let sids: any = ref<Array<string>>([]);

    //结算的购物袋商品数据
    let buyShopbagProduct: any = ref<Array<any>>([]);

    //商品总数量
    let count: any = ref<number>(0);

    //订单总价
    let total: any = ref<number>(0);

    let addAddress:() => void = (): void => {
      router.push({name: 'Address'});
    }

    let selectAddress: () => void = ():void => {
      show.value = true;
    }

    //选择地址
    let select: (item: any) => void = (item: any): void => {
      
      for (let key in userSelectAddress.value) {
        userSelectAddress.value[key] = item[key];
      }

      show.value = false;
    }

    //获取用户的地址
    let getAddressData: () => void = (): void => {
       axios({
          method: 'get',
          url: '/findAddress'
        }).then((result: any): void => {
          

          if (result.data.code === 700) {
            Toast(result.data.msg);
            router.push({name: 'Login'});
            return;
          }

          if (result.data.code === 20000) {
            result.data.result.forEach((item :any): void => {
              item.isDefault = Boolean(item.isDefault);
              item.address = `${item.province}${item.city}${item.county}${item.addressDetail}`;
              if (item.isDefault) {
                chosenAddressId.value = item.id;
                for (let key in userSelectAddress.value) {
                  userSelectAddress.value[key] = item[key];
                }
              }
            })

            list.value = result.data.result;
          }

        }).catch((err: any): void => {
          
          
        })
    }

    //获取结算的购物袋商品
    let getBuyShopbagProduct: () => void = (): void => {
     
       axios({
          method: 'get',
          url: '/commitShopcart',
          params: {
            sids: JSON.stringify(sids.value)
          }
        }).then((result: any): void => {
          

          if (result.data.code === 700) {
            Toast(result.data.msg);
            router.push({name: 'Login'});
            return;
          }

          if (result.data.code === 50000) {

            if (result.data.result.length === 0) {
              router.push({name: 'Shopbag'});
              return;
            }

            let count1: any = 0
            let total1: any = 0;
            result.data.result.forEach((item: any): void => {
              count1 += item.count;
              total1 += item.count * item.price;
            })
            buyShopbagProduct.value = result.data.result;
            count.value = count1;
            total.value = total1;
          }

        }).catch((err: any): void => {
         
          
        })
    }

    //结算
    let pay:() => void = (): void => {
      if (!chosenAddressId.value) {
        Toast('请选择地址');
        return;
      }

       axios({
          method: 'post',
          url: '/pay',
          data: {
            sids: JSON.stringify(sids.value),
            phone: userSelectAddress.value.tel,
            address: userSelectAddress.value.address,
            receiver: userSelectAddress.value.name
          }
        }).then((result: any): void => {
          
          
          Toast(result.data.msg);

           if (result.data.code === 700) {
            router.push({name: 'Login'});
            return;
          }

          if (result.data.code === 60000) {
            //跳转到订单页面
            setTimeout(() => {
              router.push({name: 'Order'});
            }, 1000)
            
          }

        }).catch((err: any): void => {
         
          
        })
    }

    onMounted(() => {
      sids.value = route.query.sids.split(/-/);
      getAddressData();
      getBuyShopbagProduct();
    })

    return {
      show,
      chosenAddressId,
      list,
      userSelectAddress,
      sids,
      buyShopbagProduct,
      count,
      total,
      addAddress,
      selectAddress,
      select,
      pay
    }
  }
})
